:export{mediumScreen:992px;smallScreen:540px}.cart{display:grid;grid-template-areas:"header" "list" "footer";grid-template-rows:min-content auto min-content;height:100%;width:25rem;max-width:100vw}.cart .overlay__header{grid-area:header;justify-content:initial;background:rgba(0,0,0,0.02)}.cart .overlay__header__close-icon{margin-left:auto}.cart__list{grid-area:list;padding:0 1rem;overflow:auto}.cart__list__item{padding:1rem 0;border-bottom:1px solid #e9e9e9;display:flex;align-content:stretch}.cart__list__item img{max-width:6.25rem;height:6.25rem}.cart__list__item__details{width:100%;padding:0 1rem}.cart__list__item__details p{text-transform:capitalize;line-height:1.5;font-size:12px}.cart__list__item__details__variant{display:flex;justify-content:space-between;margin-top:.5rem}.cart__list__item__details__variant span{color:#ababab;font-size:.875rem}.cart__list__item__details__delete-icon{background-color:#e9e9e9;border-radius:1rem;width:1.5rem;height:1.5rem;text-align:center;line-height:1.5rem;margin-top:.5rem;cursor:pointer}.cart__list__item__details__delete-icon svg *{transition:0.3s}.cart__list__item__details__delete-icon:hover svg *{fill:#7ec8ef}.cart__footer{grid-area:footer;background-color:#fff;padding:1rem;bottom:0;width:25rem}.cart__footer__price{display:flex;padding-bottom:1rem;margin-bottom:1rem;text-transform:capitalize;font-weight:600;justify-content:space-between;border-bottom:1px solid #ababab}.cart__footer__button{margin:1.3rem auto 1.3rem;text-align:center}.cart__footer__button button{width:85%}.cart__empty{padding:2rem 1rem}.cart__empty h4{font-weight:600;text-transform:capitalize;margin-bottom:1rem}.cart__empty p{color:#ababab}.cart__empty__action{text-align:center;margin-top:1rem}
