:export{mediumScreen:992px;smallScreen:540px}.post-list-item{margin-top:15px;margin-bottom:15px;text-align:center}.post-list-item__post-body{box-shadow:0 0 10px rgba(31,45,61,0.125);border-radius:5px;background:#ffffff;padding:20px;margin:-30px 20px 0;position:relative;min-height:160px;border-radius:20px}.post-list-item__post-body_title{font-size:22px;font-weight:500;margin:0 0 10px}.post-list-item__post-body_btn-bar{margin-top:20px}.post-list-item__post-body_btn-bar a{display:inline-block;color:#111111;font-size:13px;font-weight:700;letter-spacing:4px;text-transform:capitalize;padding:3px 0;position:relative}.post-list-item__image{position:relative;border-radius:5px;overflow:hidden}.post-list-item__image img{border-radius:10px;object-fit:contain;max-width:100%}.post-list-item__image__date{position:absolute;background:#3a3973;color:#ffffff;padding:8px 15px;left:0;top:10px;font-size:14px}@media (max-width: 540px){.post-list-item__image{height:210px}}
