:export{mediumScreen:992px;smallScreen:540px}.blog-page{position:relative;padding-top:75px;padding-bottom:100px}.blog-page__title{margin-bottom:65px}.blog-page__posts{margin-bottom:45px;overflow:hidden;background:#ffffff}.blog-page__header{text-align:center;padding-top:4.5rem;padding-bottom:4.5rem;background-size:cover;background-color:rgba(4,204,255,0.15);background-blend-mode:multiply}.blog-page__header__title{background-color:#fff;display:inline-block;transform:skew(-45deg);padding:1.5vw 9vw}.blog-page__header__title h1{display:inline-block;transform:skew(45deg);font-weight:600;text-transform:capitalize;color:#04ccff;font-size:8vw;font-weight:900}@media (min-width: 540px){.blog-page__header__title h1{font-size:4vw}}
