:export{mediumScreen:992px;smallScreen:540px}.product-item{font-family:Poppins, sans-serif;position:relative;z-index:1}.product-item .product-image{overflow:hidden}.product-item .product-image a{display:block}.product-item .product-image img{border-radius:10px;width:100%;height:auto;transition:all 0.5s ease 0s}.product-item:hover{box-shadow:0 0 10px rgba(0,0,0,0.3)}.product-item:hover .product-image img{transform:scale(1.1)}.product-item .product-discount-label{display:block;padding:4px 15px 4px 30px;color:#fff;background-color:#0081c2;position:absolute;top:10px;right:0;-webkit-clip-path:polygon(34% 0, 100% 0, 100% 100%, 0 100%);clip-path:polygon(34% 0, 100% 0, 100% 100%, 0 100%)}.product-item .product-content{padding:20px 0 0}.product-item .price{color:#000;font-size:19px;font-weight:400;margin-bottom:8px;text-align:left;transition:all 0.3s}.product-item .price span{color:#999;font-size:14px;font-weight:500;text-decoration:line-through;margin-left:7px;display:inline-block}.product-item .product-shipping{color:rgba(0,0,0,0.5);font-size:15px;padding-left:35px;margin:0 0 15px;display:block;position:relative}.product-item .product-shipping:before{content:"";height:1px;width:25px;background-color:rgba(0,0,0,0.5);transform:translateY(-50%);position:absolute;top:50%;left:0}.product-item .title{font-size:16px;font-weight:400;text-transform:capitalize;margin:0 0 30px;transition:all 0.3s ease 0s}.product-item .title a{color:#000}.product-item .title a:hover{color:#0081c2}.product-item .all-deals{border-radius:10px;display:block;color:#fff;background-color:#2e353b;font-size:15px;letter-spacing:1px;text-align:center;text-transform:capitalize;padding:22px 5px;transition:all 0.5s ease 0s}.product-item .all-deals .icon{margin-left:7px}.product-item .all-deals:hover{background-color:#0081c2}@media only screen and (max-width: 990px){.product-item{margin-bottom:30px}}
