:export{mediumScreen:992px;smallScreen:540px}.post-page{padding-top:125px;background-color:#fff}.post-page_mx-auto{margin-left:auto !important;margin-right:auto !important}.post-page__post{display:flex;margin-top:1rem;padding-bottom:3rem}@media (max-width: 540px){.post-page__post{display:block;margin-bottom:2rem}}.post-page__post__title{font-weight:bold;font-size:34px;color:#444;margin:15px 0 30px}.post-page__post__content{width:100%;min-height:10rem;margin-bottom:9rem;text-align:justify}.post-page__post__content blockquote{margin-top:12px;margin-bottom:12px;font-size:1.4em;font-family:Open Sans;font-style:italic;color:#555555;padding:1.2em 30px 1.2em 75px;border-left:8px solid #3a3973;line-height:1.6;position:relative;background:#fdfdfd}.post-page__post__content blockquote::before{font-family:Arial;content:"\201C";color:#3a3973;font-size:4em;position:absolute;left:10px;top:-10px}.post-page__post__content .blockquote::after{content:''}.post-page__post__content blockquote span{display:block;color:#333333;font-style:normal;font-weight:bold;margin-top:1em}.post-page__post__content ul{padding-top:6px;padding-bottom:6px;list-style-type:"*"}.post-page__post__content ul li{list-style:none;position:relative;padding:3px 0 2px 25px}.post-page__post__content ul li::before{content:'*';position:absolute;top:6px;left:0}.post-page__post__content ol{padding-top:6px;padding-bottom:6px;line-height:2}.post-page__post__content ol li{list-style-type:none;counter-increment:item}.post-page__post__content ol li:before{content:counter(item);display:inline-block;width:12px;height:20px;font-family:"Indie Flower", cursive;margin-right:5px;background-color:#f9dd94;color:#7eb4e2;font-weight:bold;font-size:140%;padding:0 8px 8px;border-radius:3px;line-height:1.3}.post-page__post__content del:before,.post-page__post__content del:after,.post-page__post__content ins:before,.post-page__post__content ins:after{clip-path:inset(100%);clip:rect(1px, 1px, 1px, 1px);height:1px;overflow:hidden;position:absolute;white-space:nowrap;width:1px}.post-page__post__content del{text-decoration-color:red}.post-page__post__info{flex-grow:1;width:30%;position:relative}@media (max-width: 992px){.post-page__post__info{width:30%}}@media (max-width: 540px){.post-page__post__info{width:100%}}@media (min-width: 540px){.post-page__post__info>div{padding-right:1rem}}.post-page__post__info--fixed{top:0;position:sticky;width:auto}@media (min-width: 1140px){.post-page__post__info--fixed{width:calc(1140px * 30 / 100)}}.post-page__post__gallery{flex-grow:2;width:60%;padding:0 1rem 0 0;box-sizing:border-box}@media (max-width: 992px){.post-page__post__gallery{width:50%}}@media (max-width: 540px){.post-page__post__gallery{width:100%;padding:0;margin-bottom:2rem;padding:0}}.post-page__post__gallery__nav li{display:inline-block;padding:.5rem}.post-page__post__gallery__nav li span{width:.5rem;height:.5rem;border:1px solid #ababab;border-radius:.5rem;display:block}.post-page__post__gallery__nav li.active span{background-color:#7ec8ef;border-color:#7ec8ef}.post-page__other-posts{background-color:#fff;padding:4rem 0}.post-page__other-posts__title{text-transform:capitalize;font-weight:600;margin-bottom:2rem}
