:export{mediumScreen:992px;smallScreen:540px}.product-page{background-color:#fff}.product-page .breadcrumbs{background-color:#fff;padding:.5rem 1rem .5rem 1.5rem;margin-top:2rem}.product-page__product{display:flex;margin-top:1rem;padding-bottom:5rem}@media (max-width: 540px){.product-page__product{display:block;margin-bottom:2rem}}.product-page__product__description{width:100%;min-height:10rem;margin-bottom:9rem}.product-page__product__info{flex-grow:1;width:30%;position:relative}@media (max-width: 992px){.product-page__product__info{width:30%}}@media (max-width: 540px){.product-page__product__info{width:100%}}@media (min-width: 540px){.product-page__product__info>div{padding-right:1rem}}.product-page__product__info--fixed{top:0;position:sticky;width:auto}@media (min-width: 1140px){.product-page__product__info--fixed{width:calc(1140px * 30 / 100)}}.product-page__product__gallery{flex-grow:2;width:60%;padding:0 1rem 0 0;box-sizing:border-box}@media (max-width: 992px){.product-page__product__gallery{width:50%}}@media (max-width: 540px){.product-page__product__gallery{width:100%;padding:0;margin-bottom:2rem;padding:0}}.product-page__product__gallery__nav li{display:inline-block;padding:.5rem}.product-page__product__gallery__nav li span{width:.5rem;height:.5rem;border:1px solid #ababab;border-radius:.5rem;display:block}.product-page__product__gallery__nav li.active span{background-color:#7ec8ef;border-color:#7ec8ef}.product-page__other-products{background-color:#fff;padding:4rem 0}.product-page__other-products__title{text-transform:capitalize;font-weight:600;margin-bottom:2rem}
