:export{mediumScreen:992px;smallScreen:540px}.article-page__container{column-gap:1.5rem;display:grid;grid-template-columns:repeat(3, 1fr);margin:60px 0}@media (max-width: 992px){.article-page__container{display:block}}.article-page__header{text-align:center;padding-top:4.5rem;padding-bottom:4.5rem;background-size:cover;background-color:#000;text-transform:capitalize}.article-page__header__breadcrumb{clear:both;color:rgba(0,0,0,0.6);float:left;font-size:13px;font-weight:500;margin:0;padding:0;width:100%}.article-page__header__title{float:left;margin:0;padding:0;pointer-events:none;position:relative;width:100%}.article-page__header__title h1{font-size:46px;color:#ffffff;font-weight:700;margin-bottom:10px}@media (min-width: 540px){.article-page__header__title h1{font-size:4vw}}.article-page__content{grid-column-end:span 2}.article-page__content h1,.article-page__content h2,.article-page__content h3{font-weight:600}.article-page__content h2{font-size:1.5rem;margin-bottom:1.5rem}.article-page__content h3{font-size:1.2rem;margin-bottom:1rem}.article-page__content img{max-width:100%}.article-page__content ol,.article-page__content ul{margin:1.875rem auto;padding-left:0;font-size:1.125rem;line-height:1.7;list-style:none}.article-page__content ol li,.article-page__content ul li{padding-left:1.3rem;margin-left:0.3rem;margin-bottom:1rem}.article-page__content ol{position:relative;counter-reset:my-awesome-counter}.article-page__content ol li{counter-increment:my-awesome-counter}.article-page__content ol li:before{content:counter(my-awesome-counter) ".";color:#06847b;position:absolute;left:0}.article-page__content ul{list-style:none}.article-page__content ul li{position:relative}.article-page__content ul li:before{position:absolute;top:0.7rem;left:0;content:"";display:inline-block;width:0.375rem;height:0.375rem;border-radius:50%;background-color:#06847b}.article-page__content ul li:last-child{margin-bottom:0}.article-page__content blockquote{position:relative;font-size:1.125rem;font-style:italic;background-color:#f1f5f5;border-left:0.5rem solid #06847b;margin:1.875rem auto;padding:3.75rem 2.5rem 3rem 2.5rem}.article-page__content blockquote:before{content:"";display:block;position:absolute;height:1.5rem;width:1.44rem;top:1.56rem;background-image:url("../../../images/quote-icon.svg");background-repeat:no-repeat;background-size:contain}@media (max-width: 992px){.article-page__navigation{margin-bottom:2rem}}.article-page__navigation-element{font-family:"Montserrat";font-size:1.5rem;font-weight:600;text-transform:capitalize}.article-page__navigation-element--active a{display:inline-block;position:relative}.article-page__navigation-element--active a:before{background-color:#7ec8ef;content:"";height:7px;left:0;position:absolute;top:6px;transform:skew(45deg);width:100%;z-index:-1}
