:export{mediumScreen:992px;smallScreen:540px}.search{width:100%;height:auto;padding-bottom:0}.search_search-group{position:relative;max-width:768px;margin:0 auto 30px;display:flex;align-items:center;border-bottom:2px solid currentColor}.search_search-group__icon{left:0;margin:0;width:20px;height:20px;opacity:0.5;display:flex;align-items:center}.search_side-panel{display:flex;flex-direction:column;position:fixed;top:0;max-height:280px;width:100%;z-index:600;padding:0;background:#fff;right:0;will-change:transform;-webkit-transition:opacity 0.25s ease, ease;transition:opacity 0.25s ease, transform 0.25s ease}.search_side-panel_headerr{background:0 0;height:auto;border:0;padding:0;justify-content:flex-end;padding:50px 0 20px;font-size:1.1em}.search_side-panel_headerr-inner{padding:50px 0 20px;font-size:1.1em;background:0 0;height:auto;border:0;padding:0;justify-content:flex-end}.search_side-panel_headerr-inner-remove{float:right;border-radius:50%;background:#f3f3f3;border:9px solid transparent;color:#777;width:45px;height:45px;display:inline-flex;justify-content:center;align-items:center;position:relative}@media (min-width: 992px){.search{width:25rem}}.search--has-results{min-height:100vh}.search .input{margin-bottom:0}.search__input .input__icon-right svg *{fill:#7ec8ef}.search__input .input__icon-left svg *{fill:#323232}.search__input .input__field{border:none;box-shadow:none;padding-bottom:1.2rem;padding-top:1.25rem;width:100%}.search__input .input__field:focus{box-shadow:none}.search__input .input__field::placeholder{text-transform:capitalize;font-size:1rem;color:#000}.search__products{background:#fff;overflow:auto;padding:.5rem 0;position:relative;right:0;transition:300ms;height:calc(100vh - 3rem)}@media (min-width: 992px){.search__products{right:-25rem}}.search__products--expanded{right:0}.search__products--not-found{padding:14px}.search__products__item{padding:.5rem 1rem}.search__products__item img{width:3.6rem;height:3.6rem;display:inline-block;margin-right:1rem;vertical-align:top}.search__products__item span{display:inline-block;width:calc(100% - 4.6rem)}.search__products__item h4{text-transform:capitalize;font-weight:600;padding:.25rem 0;color:#323232}.search__products__item p{color:#ababab}.search__products__item:hover{background-color:#e9e9e9}.search__products__item ul{max-height:calc(100vh - 9rem)}.search__products__footer{position:fixed;bottom:0px;border-top:1px solid #e9e9e9;text-align:center;padding:1rem 0;background-color:#fff;width:100vw}@media (min-width: 992px){.search__products__footer{width:25rem}}
